@tailwind base;
@tailwind components;
@tailwind utilities;

/* Global Styles */
body {
  @apply bg-no-repeat bg-cover bg-center;
  backbackground-image: url('https://images.pexels.com/photos/104827/cat-pet-animal-domestic-104827.jpeg?auto=compress&cs=tinysrgb&w=1260&h=750&dpr=2'); /* Direct URL to a cat image */
  font-family: Arial, sans-serif;
  margin: 0;
  padding: 0;
}

/* Custom Animations */
.animate-fadeIn {
  animation: fadeIn 2s ease-in-out;
}

.animate-pulse {
  animation: pulse 2s infinite ease-in-out;
}

/* Keyframes */
@keyframes fadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

@keyframes pulse {
  0%, 100% {
    transform: scale(1);
  }
  50% {
    transform: scale(1.05);
  }
}